import * as Yup from "yup";

export const userValidationSchema = Yup.object().shape({
  taiKhoan: Yup.string().required("(*) Ti khon khng c  trng"),
  matKhau: Yup.string().required("(*) Mt khu khng c  trng"),
  email: Yup.string()
    .required("(*) Email khng c  trng")
    .matches(
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      "(*) Email cha ng nh dng"
    ),
  soDt: Yup.string()
    .required("(*) S in thoi khng c  trng")
    .matches(/^[0-9]+$/, "(*) Vui lng nhp s"),
  hoTen: Yup.string().required("(*) H tn khng c  trng")
});
